import * as core from '@actions/core';
import { run } from './run.js';
const main = async () => {
    const outputs = await run({
        rules: core.getInput('rules', { required: true }),
        token: core.getInput('token'),
    });
    core.setOutput('json', outputs.json);
};
main().catch((e) => {
    core.setFailed(e);
    console.error(e);
});
